body {
  font-family: var(--font-main), "Arial", sans-serif;
  font-weight: 400;
  font-size: 14px;
  line-height: 1.2;
  color: var(--color-brown-600);
  background-color: var(--color-white);
}

.container {
  margin: 0 auto;
  padding: 0 24px;
  width: 100%;
  max-width: 1168px;
}

.wrapper {
  display: flex;
  flex-direction: column;
  overflow: hidden;
  min-height: 100vh;
}

.main {
  flex: 1 1 auto;
}

.sr-only {
  position: absolute;
  clip-path: inset(100%);
  overflow: hidden;
  margin: 1px;
  border: 0;
  padding: 0;
  width: 1px;
  height: 1px;
  white-space: nowrap;
  clip: rect(0 0 0 0);
}

.disable-scroll {
  position: fixed;
  left: 0;
  top: 0;
  overflow: hidden;
  width: 100%;
  height: 100vh;
}

.logo {
  display: inline-block;
  border-radius: 4px;
  width: 100%;
  height: 78px;
  max-width: 238px;
  transition: box-shadow 0.3s;
}

.logo:focus-visible {
  box-shadow: 0 0 0 2px var(--color-red-800);
}

.logo__img {
  width: 100%;
  height: 100%;
  object-fit: contain;
}

.swiper-slide {
  box-sizing: border-box;
}

.overlay {
  position: fixed;
  left: 0;
  top: 0;
  z-index: -1;
  width: 100%;
  height: 100%;
  background-color: var(--color-black);
  opacity: 0;
}

.overlay--active {
  z-index: 4;
  opacity: 0.5;
}

.button {
  display: flex;
  flex-direction: row;
  align-items: center;
  justify-content: center;
  gap: 8px;
  border-radius: 8px;
  padding: 8px 16px;
  font-family: var(--font-secondary);
  font-weight: 700;
  font-size: 14px;
  line-height: 1.1;
}

.button--secondary {
  border: 1px solid var(--color-grey-270);
  border-radius: 55px;
  font-size: 16px;
  color: var(--color-dark-800);
  transition: box-shadow 0.3s, border-color 0.3s;
}

.button--secondary:focus-visible {
  border-color: transparent;
  box-shadow: 0 0 0 2px var(--color-red-300);
}

.button--secondary:active {
  border-color: transparent;
  box-shadow: 0 0 0 2px var(--color-red-500);
}

.button--fill {
  color: var(--color-white);
  background-color: var(--color-red-500);
  transition: background-color 0.3s, box-shadow 0.3s;
}

.button--fill:focus-visible {
  box-shadow: 0 0 0 2px var(--color-red-300);
  background-color: var(--color-red-500);
}

.button--fill:active {
  background-color: var(--color-red-850);
}

.button--fill:disabled {
  background-color: var(--color-grey-600);
}

.button--border {
  border: 1px solid var(--color-red-500);
  color: var(--color-red-500);
  transition: background-color 0.3s, box-shadow 0.3s, border-color 0.3s, color 0.3s;
}

.button--border:focus-visible {
  border-color: var(--color-red-450);
}

.button--border:active {
  background-color: var(--color-red-200);
}

.button--border:disabled {
  border-color: var(--color-grey-600);
  color: var(--color-grey-600);
}

.button__icon {
  width: 16px;
  height: 16px;
}

.hotline__link {
  display: block;
  font-weight: 600;
  font-size: 16px;
}

.hotline__descr {
  font-size: 11px;
  color: var(--color-red-500);
}

.choices[data-type*=select-one]::after {
  background-position: center;
  background-size: contain;
  background-repeat: no-repeat;
  right: 10px;
  margin-top: 0;
  border: 0;
  width: 20px;
  height: 20px;
  background-image: url("../img/icons/arrow.svg");
  transform: rotate(-180deg) translateY(50%);
  transition: transform 0.3s;
}

.choices[data-type*=select-one].is-open::after {
  margin-top: 0;
  transform: rotate(0) translateY(-50%);
}

.choices__inner {
  display: flex;
  align-items: center;
  border: 0;
  border-radius: 0;
  border-top-left-radius: 4px;
  border-bottom-left-radius: 4px;
  padding: 10px 20px 0;
  color: var(--color-white);
  background-color: var(--color-brown-600);
}

.choices__item {
  font-weight: 600;
  font-size: 13px;
  color: var(--color-grey-420);
  transition: color 0.3s;
}

.choices.is-open .choices__inner {
  border-color: transparent;
  border-radius: 0;
  border-top-left-radius: 4px;
}

.choices.is-focused .choices__inner {
  border-color: transparent;
}

.choices__list {
  color: var(--color-white);
  background-color: var(--color-brown-600);
}

.choices__list--dropdown,
.choices__list[aria-expanded] {
  margin-top: 0;
  border: 0;
  border-bottom-right-radius: 4px;
  border-bottom-left-radius: 4px;
  padding: 12px 16px 0;
  background-color: var(--color-brown-600);
}

.choices__list--dropdown .choices__item,
.choices__list[aria-expanded] .choices__item {
  padding: 0;
  padding-bottom: 10px;
  font-size: 13px;
}

.choices__list--dropdown .choices__item:not(:first-child),
.choices__list[aria-expanded] .choices__item:not(:first-child) {
  border-top: 1px solid var(--color-brown-300);
  padding-top: 10px;
}

.choices__list--dropdown .choices__item--selectable.is-highlighted,
.choices__list[aria-expanded] .choices__item--selectable.is-highlighted {
  color: var(--color-white);
  background-color: var(--color-brown-600);
}

.choices__list--single {
  padding: 0;
  padding-right: 20px;
}

.choices__list--single .choices__item {
  color: var(--color-white);
}

.download-info {
  display: flex;
  font-size: 12px;
  color: var(--color-grey-100);
  gap: 20px;
}

.download-info__content {
  display: flex;
  flex-direction: column;
  gap: 12px;
}

.download-info__link {
  position: relative;
  padding-right: 24px;
  width: max-content;
  color: var(--color-red-500);
}

.download-info__link::before {
  background-position: center;
  background-size: contain;
  background-repeat: no-repeat;
  content: "";
  position: absolute;
  right: 0;
  top: 0;
  width: 16px;
  height: 16px;
  background-image: url("../img/icons/arrow-long.svg");
  filter: invert(62%) sepia(26%) saturate(3774%) hue-rotate(340deg) brightness(99%) contrast(89%);
}

.download-info__qr {
  display: flex;
  flex-direction: row;
  align-items: center;
  justify-content: center;
  gap: 0;
  flex-shrink: 0;
  flex-basis: 60px;
  border-radius: 8px;
  padding: 12px;
  height: 60px;
  background-color: var(--color-white);
}

.download-info__img {
  width: 48px;
  height: 48px;
  object-fit: contain;
}

.block-header {
  display: flex;
  flex-direction: row;
  align-items: center;
  justify-content: space-between;
  gap: 20px;
  margin-bottom: 32px;
  border-bottom: 2px solid var(--color-red-500);
  padding-bottom: 16px;
}

.block-header__title {
  margin-bottom: 0;
  font-family: var(--font-secondary);
  font-weight: 700;
  font-size: 24px;
}

.block-header__title sup {
  font-size: 14px;
  color: var(--color-red-500);
}

.block-header__link {
  font-size: 13px;
  color: var(--color-red-500);
}

.form__item {
  width: 100%;
}

.form__item--search {
  position: relative;
}

.form__item--search .form__field {
  border-color: var(--color-brown-600);
  padding: 12px 45px 12px 18px;
}

.form__item--search .form__field::placeholder {
  color: var(--color-dark-800);
}

.form__item--search .form__button {
  position: absolute;
  right: 15px;
  top: 50%;
  width: 16px;
  height: 16px;
  color: var(--color-brown-600);
  transform: translateY(-50%);
}

.form__item--search .form__button svg {
  width: 100%;
  height: 100%;
}

.form__field {
  border: 1px solid var(--color-grey-450);
  border-radius: 0 4px 4px 0;
  padding: 8px;
  width: 100%;
  background-color: transparent;
  outline: 0;
  transition: border-color 0.3s, background-color 0.3s;
}

.form__field:focus {
  border-color: var(--color-red-500);
}

.form__field::placeholder {
  color: var(--color-grey-600);
}

.form__field:disabled {
  background-color: var(--color-grey-20);
}

.form__field--error {
  border-color: var(--color-red-900);
}

.header {
  position: relative;
  z-index: 2;
}

.header__top {
  position: relative;
  z-index: 20;
  flex-shrink: 0;
  background-color: var(--color-white);
}

.header__head {
  display: flex;
  flex-direction: row;
  align-items: center;
  justify-content: space-between;
  gap: 40px;
}

.header__menu {
  width: 100%;
}

.header__info {
  display: flex;
  padding-top: 12px;
  gap: 32px;
}

.header__name {
  margin-right: auto;
  width: 100%;
  max-width: 240px;
  font-weight: 700;
}

.header .form__field {
  height: 100%;
}

.header__search {
  position: relative;
  display: flex;
}

.header__select {
  max-width: 200px;
}

.burger-button {
  --burger-width: 18px;
  --burger-height: 18px;
  --burger-line-height: 2px;
  --burger-border-radius: 0;
  --burger-color: var(--color-red-500);
  position: relative;
  width: var(--burger-width);
  height: var(--burger-height);
  color: var(--burger-color);
}

.burger-button::before,
.burger-button::after {
  content: "";
  position: absolute;
  left: 0;
  width: 100%;
  height: var(--burger-line-height);
  background-color: currentcolor;
  transition: transform 0.3s ease-in-out, top 0.3s ease-in-out;
}

.burger-button::before {
  top: 0;
}

.burger-button::after {
  top: calc(100% - var(--burger-line-height));
}

.burger-button__line {
  position: absolute;
  right: 0;
  top: 50%;
  width: 70%;
  height: var(--burger-line-height);
  background-color: currentcolor;
  transform: translateY(-50%);
  transition: transform 0.3s ease-in-out;
}

.burger-button--active::before,
.burger-button--active::after {
  top: 50%;
  transition: transform 0.3s ease-in-out, top 0.3s ease-in-out;
}

.burger-button--active::before {
  transform: rotate(45deg);
}

.burger-button--active::after {
  transform: rotate(-45deg);
}

.burger-button--active .burger-button__line {
  transform: scale(0);
  transition: transform 0.3s ease-in-out;
}

.header .menu {
  display: flex;
  align-items: center;
  color: var(--color-brown-300);
  gap: 45px;
}

.header .menu__private {
  padding: 12px 16px;
  width: 100%;
}

.header .menu__items {
  display: flex;
  flex-direction: column;
  gap: 24px;
  margin-bottom: 25px;
  width: 100%;
  font-size: 16px;
}

.header .menu__item--highlight {
  border-bottom: 1px solid var(--color-red-500);
  padding-bottom: 24px;
}

.header .menu__version {
  position: relative;
  padding-left: 42px;
}

.header .menu__version::before {
  background-position: center;
  background-size: contain;
  background-repeat: no-repeat;
  content: "";
  position: absolute;
  left: 0;
  top: 50%;
  width: 30px;
  height: 10px;
  background-image: url("../img/icons/glasses.svg");
  transform: translateY(-50%);
}

.header .select-mobile {
  display: none;
  color: var(--color-white);
}

.header .select-mobile__list {
  position: absolute;
  left: 0;
  bottom: 2px;
  display: none;
  flex-direction: column;
  border-radius: 0 0 4px 4px;
  padding: 12px 16px;
  width: 100%;
  background-color: var(--color-brown-600);
  transform: translateY(100%);
}

.header .select-mobile__list--active {
  display: flex;
}

.header .select-mobile__item {
  position: relative;
  color: var(--color-grey-420);
  transition: color 0.3s;
}

.header .select-mobile__item::before {
  background-position: center;
  background-size: contain;
  background-repeat: no-repeat;
  content: "";
  position: absolute;
  right: 0;
  top: 50%;
  width: 16px;
  height: 16px;
  background-image: url("../img/icons/arrow.svg");
  opacity: 0;
  transform: translateY(-70%);
  transition: opacity 0.3s;
}

.header .select-mobile__item--active {
  color: var(--color-white);
}

.header .select-mobile__item--active::before {
  opacity: 1;
}

.header .select-mobile__item:not(:last-child) {
  border-bottom: 1px solid var(--color-brown-300);
  padding-bottom: 8px;
}

.header .select-mobile__item:not(:first-child) {
  padding-top: 8px;
}

.header .select-mobile__hidden {
  position: absolute;
  overflow: hidden;
  width: 1px;
  height: 1px;
  clip: rect(0 0 0 0);
}

.header .select-mobile__button {
  display: flex;
  flex-direction: row;
  align-items: center;
  justify-content: center;
  gap: 0;
  border-radius: 4px 0 0 4px;
  width: 40px;
  min-height: 40px;
  background-color: var(--color-brown-600);
}

.header .select-mobile__button svg {
  width: 16px;
  height: 16px;
}

.socials {
  display: flex;
  align-items: center;
  gap: 25px;
}

.socials__icon {
  width: 16px;
  height: 16px;
  transition: fill 0.3s;
}

.footer__wrapper {
  display: flex;
  flex-direction: row;
  gap: 24px;
  position: relative;
  justify-content: space-between;
  border-top: 3px solid var(--color-red-500);
  border-radius: 4px 4px 0 0;
  padding: 24px 24px 16px;
  width: 100%;
  background-color: var(--color-brown-600);
}

.footer__decor {
  position: absolute;
  left: -2%;
  bottom: -75%;
  transform: rotate(-50deg);
}

.footer__decor::before,
.footer__decor::after {
  content: "";
  position: absolute;
  border: 1px solid var(--color-red-500);
  border-radius: 70%;
}

.footer__decor::before {
  left: 60px;
  top: -40px;
  width: 212px;
  height: 60px;
}

.footer__decor::after {
  width: 358px;
  height: 100px;
}

.footer__menu {
  display: flex;
  flex-wrap: wrap;
  width: 44%;
  font-size: 12px;
  color: var(--color-grey-420);
  gap: 20px;
}

.footer__item {
  flex-basis: 45%;
}

.footer__column {
  width: 22%;
}

.footer__column--copy {
  font-size: 10px;
  color: var(--color-grey-550);
}

.footer__column--info {
  display: flex;
  flex-direction: column;
  gap: 30px;
}

.footer .hotline__link {
  color: var(--color-grey-200);
}

.service-card {
  display: flex;
  flex-direction: column;
  align-items: flex-start;
  justify-content: space-between;
  gap: 10px;
  position: relative;
  z-index: 1;
  overflow: hidden;
  border: 1px solid var(--color-grey-100);
  border-radius: 4px;
  padding: 16px 82px 0 16px;
  width: 100%;
  height: 100%;
  background-image: linear-gradient(180deg, #f0f0f0 0, #f2f2f2 100%);
}

.service-card::before,
.service-card::after {
  content: "";
  position: absolute;
}

.service-card::before {
  inset: 0;
  z-index: -1;
  border-radius: 4px;
  background-image: linear-gradient(72.84deg, #ff6e56 5.67%, #ef5238 121.39%);
  opacity: 0;
  transition: opacity 0.5s;
}

.service-card::after {
  background-position: center;
  background-size: contain;
  background-repeat: no-repeat;
  right: 16px;
  bottom: 16px;
  width: 24px;
  height: 24px;
  background-image: url("../img/icons/arrow-long.svg");
  filter: invert(77%) sepia(15%) saturate(1753%) hue-rotate(333deg) brightness(98%) contrast(89%);
  transition: filter 0.3s;
}

.service-card__title {
  font-weight: 600;
  font-size: 13px;
  line-height: 1.3;
  transition: color 0.3s;
}

.service-card__title--large {
  font-size: 16px;
  line-height: 1.2;
}

.service-card__title a::before {
  content: "";
  position: absolute;
  inset: 0;
  z-index: 1;
}

.service-card picture {
  display: inline-block;
  flex-shrink: 0;
  flex-basis: 94px;
  height: 94px;
}

.service-card__img {
  width: 100%;
  height: 100%;
  object-fit: contain;
}

.service-card__bg {
  background-position: center;
  background-size: contain;
  background-repeat: no-repeat;
  position: absolute;
  left: 0;
  bottom: 0;
}

.text-card {
  display: flex;
  flex-direction: row;
  align-items: center;
  justify-content: space-between;
  gap: 16px;
  border-bottom: 1px solid var(--color-grey-270);
  border-radius: 4px;
  padding: 16px 0;
  width: 100%;
}

.text-card__text {
  transition: color 0.3s;
}

.text-card__link {
  flex-shrink: 0;
  max-width: max-content;
  line-height: 1.3;
}

.services-block {
  padding: 32px 0 64px;
}

.services-block__items {
  display: grid;
  grid-template-columns: repeat(4, 1fr);
  gap: 32px;
}

.popular-services {
  padding-top: 54px;
}

.popular-services__items {
  display: flex;
  flex-wrap: wrap;
  gap: 8px 30px;
}

.popular-services__items:not(:last-child) {
  margin-bottom: 24px;
}

.popular-services__link {
  color: var(--color-red-500);
}

.partners {
  padding: 54px 0 80px;
}

.partners__wrapper {
  position: relative;
  margin: 0 auto;
  width: 100%;
  max-width: 1220px;
}

.partners__swiper {
  position: static;
}

.partners__items {
  padding: 2px 0;
  width: 100%;
}

.partners__item {
  width: 256px;
  height: 86px;
}

.partners__button {
  background-position: center;
  background-size: contain;
  background-repeat: no-repeat;
  position: absolute;
  top: 50%;
  width: 24px;
  height: 24px;
  background-image: url("../img/icons/arrow.svg");
  filter: invert(64%) sepia(67%) saturate(1472%) hue-rotate(338deg) brightness(98%) contrast(91%);
}

.partners__button--next {
  right: -50px;
  transform: rotate(90deg) translateX(-50%);
}

.partners__button--prev {
  left: -50px;
  transform: rotate(-90deg) translateX(50%);
}

.partner-card {
  width: 100%;
  height: 100%;
}

.partner-card__link {
  display: flex;
  flex-direction: row;
  align-items: center;
  justify-content: center;
  gap: 0;
  border: 1px solid var(--color-grey-270);
  border-radius: 4px;
  padding: 16px;
}

.partner-card__img {
  width: 180px;
  height: 54px;
  object-fit: contain;
}

@media (min-width: 640px) {
  .choices__list--dropdown .choices__item--selectable[data-select-text],
  .choices__list[aria-expanded] .choices__item--selectable[data-select-text] {
    padding-right: 100px;
  }

  .choices__list--dropdown .choices__item--selectable[data-select-text]::after,
  .choices__list[aria-expanded] .choices__item--selectable[data-select-text]::after {
    content: attr(data-select-text);
    font-size: 12px;
    opacity: 0;
    position: absolute;
    right: 10px;
    top: 50%;
    transform: translateY(-50%);
  }

  [dir=rtl] .choices__list--dropdown .choices__item--selectable[data-select-text],
  [dir=rtl] .choices__list[aria-expanded] .choices__item--selectable[data-select-text] {
    text-align: right;
    padding-left: 100px;
    padding-right: 10px;
  }

  [dir=rtl] .choices__list--dropdown .choices__item--selectable[data-select-text]::after,
  [dir=rtl] .choices__list[aria-expanded] .choices__item--selectable[data-select-text]::after {
    right: auto;
    left: 10px;
  }
}

@media only screen and (min-width: 993px) {
  .header {
    padding-top: 12px;
  }

  .header__container {
    margin: 0 auto;
    padding: 0 24px;
    max-width: 1168px;
  }

  .header__wrapper {
    display: flex;
    margin-bottom: 35px;
    width: 100%;
    gap: 50px;
  }

  .header__inner {
    width: 100%;
  }

  .header__menu {
    border-bottom: 1px solid var(--color-orange-300);
    padding-bottom: 12px;
  }

  .burger-button {
    display: none;
  }

  .header .menu__private {
    display: none;
    padding: 0;
  }

  .header .menu__items {
    flex-direction: row;
    margin-right: auto;
    margin-bottom: 0;
    width: auto;
    font-size: 13px;
    gap: 32px;
  }

  .header .menu__item--mobile {
    display: none;
  }

  .header .menu .hotline {
    display: none;
  }

  .header .menu .hotline__link {
    font-size: 24px;
  }

  .header .menu .hotline__descr {
    font-size: 12px;
  }

  .service-card__img {
    object-position: left bottom;
  }

  .popular-services__item {
    flex-basis: calc(50% - 15px);
  }

  .popular-services__link {
    display: none;
  }
}

@media only screen and (max-width: 993px) {
  .logo {
    height: 38px;
    max-width: 120px;
  }

  .block-header {
    margin-bottom: 24px;
  }

  .block-header__link {
    display: none;
  }

  .header__top {
    display: flex;
    flex-direction: row;
    align-items: center;
    justify-content: space-between;
    gap: 20px;
    flex-shrink: unset;
    padding: 16px 24px;
  }

  .header__info {
    display: none;
  }

  .header .form {
    padding: 0 24px;
  }

  .header__select {
    display: none;
  }

  .header .menu {
    position: fixed;
    right: 0;
    top: 0;
    display: none;
    flex-direction: column;
    align-items: flex-start;
    overflow: hidden;
    padding: 90px 24px 36px;
    width: 100%;
    background-color: var(--color-white);
    visibility: hidden;
    transform: translateX(100%);
    transition: transform 0.5s;
  }

  .header .menu--active {
    right: 0;
    z-index: 5;
    display: flex;
    overflow-y: auto;
    height: 100vh;
    opacity: 1;
    visibility: visible;
    transform: translateX(0);
    transition: transform 0.3s ease-in-out;
  }

  .header .menu__version {
    order: 1;
  }

  .header .select-mobile {
    display: flex;
  }

  .footer__container {
    padding: 0;
  }

  .footer__wrapper {
    border-radius: 0;
    padding-bottom: 12px;
  }

  .footer__decor {
    left: -12%;
    bottom: -84%;
  }

  .service-card {
    display: flex;
    flex-direction: row-reverse;
    align-items: center;
    justify-content: flex-end;
    gap: 16px;
    padding: 16px 50px 16px 16px;
  }

  .service-card::after {
    bottom: 50%;
    transform: translateY(50%);
  }

  .service-card picture {
    flex-basis: 54px;
    height: 48px;
  }

  .text-card {
    flex-direction: column;
    align-items: flex-start;
    justify-content: flex-start;
  }

  .services-block__items {
    grid-template-columns: 1fr 1fr;
    gap: 16px;
  }

  .popular-services {
    padding-top: 40px;
  }

  .popular-services__items {
    flex-direction: column;
  }

  .partners {
    padding: 44px 0 64px;
  }

  .partners__button {
    display: none;
  }
}

@media only screen and (max-width: 769px) {
  .container {
    padding: 0 16px;
  }

  .download-info__qr {
    display: none;
  }

  .header .form {
    padding: 0 16px;
  }

  .header .menu {
    padding: 85px 16px 16px;
  }

  .footer__container {
    padding: 0;
  }

  .footer__wrapper {
    flex-wrap: wrap;
    padding: 24px 16px 16px;
  }

  .footer__decor {
    left: 16%;
    bottom: -80%;
  }

  .footer__menu {
    width: 60%;
    gap: 12px;
  }

  .footer__column {
    width: 30%;
  }

  .footer__column--copy {
    order: 1;
  }

  .services-block {
    padding: 40px 0;
  }

  .services-block__items {
    grid-template-columns: 1fr;
  }

  .partners__container {
    padding-right: 0;
  }
}

@media only screen and (max-width: 561px) {
  .footer__decor {
    left: 30%;
    bottom: -72%;
  }

  .footer__menu {
    width: 100%;
  }

  .footer__item {
    flex-grow: 1;
    flex-basis: 48%;
  }

  .footer__column--info {
    display: flex;
    flex-direction: row;
    gap: 20px;
    width: 100%;
  }

  .footer .hotline,
  .footer .download-info {
    flex-basis: 50%;
  }

  .services-block {
    padding: 24px 0;
  }
}

@media (any-hover: hover) {
  .button--secondary:hover {
    border-color: transparent;
    box-shadow: 0 0 0 2px var(--color-red-500);
  }

  .button--fill:hover {
    background-color: var(--color-red-800);
  }

  .button--border:hover {
    background-color: var(--color-red-100);
  }

  .form__field:hover {
    border-color: var(--color-red-500);
  }

  .socials__link:hover {
    color: var(--color-red-500);
  }

  .service-card:hover::before {
    opacity: 1;
  }

  .service-card:hover::after {
    filter: none;
  }

  .service-card:hover .service-card__title {
    color: var(--color-white);
  }

  .text-card__text:hover {
    color: var(--color-red-500);
  }
}

@media (width >= 640px) {
  .choices__list--dropdown .choices__item--selectable[data-select-text],
  .choices__list[aria-expanded] .choices__item--selectable[data-select-text] {
    padding-right: 0;
  }
}